import './App.scss';
import React from 'react';
import "../node_modules/bootstrap/dist/css/bootstrap.css";
let marked = require("marked")

const initialState = `
# Welcome to my React Markdown Previewer!

## This is a sub-heading...
### And here's some other cool stuff:

Heres some code, \`<div></div>\`, between 2 backticks.

\`\`\`
 let i = 5;
 let z = 2;
\`\`\`

You can also make text **bold**... whoa!
Or _italic_.
Or... wait for it... **_both!_**
And feel free to go crazy ~~crossing stuff out~~.

There's also [links](https://www.freecodecamp.com), and
> Block Quotes!

And if you want to get really crazy, even tables:

Wild Header | Crazy Header | Another Header?
------------ | ------------- | -------------
Your content can | be here, and it | can be here....
And here. | Okay. | I think we get it.

- And of course there are lists.
  - Some are bulleted.
     - With different indentation levels.
        - That look like this.


1. And there are numbererd lists too.
1. Use just 1s if you want!
1. And last but not least, let's not forget embedded images:

![React Logo w/ Text](https://goo.gl/Umyytc)


`

class App extends React.Component{
  constructor(props) {
    super(props);
    this.state ={
      text:initialState
    };
    this.handleChange = this.handleChange.bind(this);
  }
  
  handleChange =(e)=>{
    this.setState({
      text: e.target.value
    })
  }
  render(){
    const{text}=this.state;
    const markdown = marked(text);
    return (
      <div>
       <h1 className="text-center" style={{color:"white"}}>
         Convert you Markdown
       </h1>

       <div className="row">
       <div class="col-12 col-sm-12 col-md-12 col-lg-6 col-xl-6 d-flex flex-column">
            <h4 className="text-center" style={{color:"white",marginTop:"10px" }}>Editor</h4>
          <div className="form-group flex-grow-1 d-flex flex-column">
           <textarea id="editor" value={text}onChange={this.handleChange}className="form-control flex-grow-1"  ></textarea>
          </div>
           </div>
           <div className="col-12 col-sm-12 col-md-12 col-lg-6 col-xl-6 d-flex flex-column">
            <h4 className="text-center" style={{color:"white"}}>Previewer</h4>
            <div id="preview" dangerouslySetInnerHTML={{__html: marked(markdown)}}>
            
            </div>
           </div>
       </div>

      </div>
    )
  }
}

export default App;
